import {observable, computed, action, autorun} from 'mobx';


class appleStore {

    @observable apples = [
        {
            id: 0,
            weight: 233,
            isEaten: false
        },
        {
            id: 1,
            weight: 235,
            isEaten: true
        },
        {
            id: 2,
            weight: 256,
            isEaten: false
        }
    ];
    @observable newAppleId = 3;
    @observable isPicking = false;
    @observable buttonText = '';




    /**   */
    @computed get status(){
        let status = {
            appleNow: {
                quantity: 0,
                weight: 0
            },
            appleEaten: {
                quantity: 0,
                weight: 0
            }
        };
        this.apples.forEach(apple => {
            let selector = apple.isEaten ? 'appleEaten':'appleNow';
            status[selector].quantity ++;
            status[selector].weight += apple.weight;
        });
        return status;
    }


    /**/
    @action pickApple = ()=>{

        /** thunk,  */
        if (this.isPicking) {
            return;
        }

        this.isPicking = true;
        this.buttonText = '...';
        setTimeout(() => {
            let weight = Math.floor(200 + Math.random() * 50);
                this.isPicking = false;
                this.buttonText = '';
                this.apples.push({
                    id: this.newAppleId++,
                    weight: weight,
                    isEaten: false
                });
        },1000)
        // fetch('https://hacker-news.firebaseio.com/v0/jobstories.json')
        //     .then(res => {
        //         /** urlhackernewsapi, mock */
        //         let weight = Math.floor(200 + Math.random() * 50);
        //         this.isPicking = false;
        //         this.buttonText = '';
        //         this.apples.push({
        //             id: this.newAppleId++,
        //             weight: weight,
        //             isEaten: false
        //         });
        //     });
    }

    /* */
    @action eatApple = (appleId)=>{

        let targetIndex = '';
        this.apples.forEach((apple,index)=>{
            if(apple.id == appleId){
                targetIndex = index
            }
        });
        this.apples[targetIndex].isEaten = true;
    }




}

export default appleStore;


